import './imageDetails.css'

const Spaceweather = () => {
    return (
        <main className='imageinfo-main'>
            <h2>Геомагнитная обстановка</h2>
            <h3>Солнечные пятна в реальном времени</h3>
            <p>
            <a href='https://spaceweather.com/images2021/13dec21/hmi1898.gif' target='_blank'><img className='min' src='https://spaceweather.com/images2021/13dec21/hmi200.gif' width='200' /></a></p>
            <h3>Текущая зона полярных сияний</h3>
            <p><a href='https://services.swpc.noaa.gov/images/aurora-forecast-northern-hemisphere.jpg' target='_blank'><img src='https://spaceweather.com/POES/pics/europe_thumb.jpg' className='min' /></a></p>
            <h3>График текущей геомагнитной обстановки</h3>
            <p><a href='https://services.swpc.noaa.gov/images/ace-mag-swepam-2-hour.gif' target='_blank'><img src='https://services.swpc.noaa.gov/images/ace-mag-swepam-2-hour.gif' className='full' /></a></p>
        </main>
    )
}

export default Spaceweather